.bulk-operations-sheet-container {
  display: flex;
  flex-direction: column;
  height: auto !important;
  min-height: 100vh;
  background: white !important;
  /* gap: 1rem; */
}
.animated-container {
  transition: all 0.5s ease;
}
.tabs {
  position: sticky;
  top: var(--ir-tabs-top, 54px);
  background-color: white;
  z-index: 9999999;
  padding-top: 1rem;
  margin-bottom: 1rem;
}
